.chart-container{
    display: flex;
    height: 400px;
    width: 100%;
    transform: scale(1.1);
}

.chart-container-smaller-screen{
    display: none;
}

@media (max-width: 768px){
    .chart-container{
        display: none;
    }

    .chart-container-smaller-screen{
        display: flex;
        flex-direction: column;
        height: 800px;
    }
}